import { useEffect, useState } from 'react';
import { ShareApi, ShareSaveApi } from 'utils/Api';
import { Button, Upload, message, Form, Input, Space, Card } from 'antd';
import { PlusOutlined, MinusCircleOutlined } from '@ant-design/icons';

const Share = () => {
  const [imageUrl, setImageUrl] = useState(null);
  const [form] = Form.useForm();

  useEffect(() => {
    ShareApi().then((res) => {
      setImageUrl(res.data.imageUrl);
			form.setFieldsValue({notes: res.data.notes, rules: res.data.rules})
    });
  }, []);

  const onChange = (info, type) => {
    if (info.file.status === 'done') {
      message.success('文件上传成功');
        setImageUrl(info.file.response);
    } else if (info.file.status === 'error') {
      message.error('文件上传失败');
    }
  };

  const onFinish = (value) => {
    ShareSaveApi({ imageUrl, ...value }).then((res) => {
      message.success('保存成功');
    });
  };
  return (
    <Card title="分享页面设置" bordered={false} style={{ width: 600 }}>
      <Form
        name="control-hooks"
        onFinish={onFinish}
				form={form}
      >
        <Form.Item label="页面横幅">
          <Upload
              style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
              }}
            name="file"
            action={process.env.REACT_APP_UPLOAD_SERVER}
            headers={
              localStorage.getItem('__admin_token')
                ? { Authorization: `Bearer ${localStorage.__admin_token}` }
                : null
            }
            showUploadList={false}
            onChange={(info) => onChange(info, 'img')}
          >
            {imageUrl ? (
              <img
                src={process.env.REACT_APP_SERVER + imageUrl}
                alt="avatar"
                  style={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    width: '6rem',
                    height: '6rem',
                  }}
              />
            ) : (
              <div
                  style={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    width: '12rem',
                    height: '12rem',
                  }}>
                <PlusOutlined />
              </div>
            )}
          </Upload>
        </Form.Item>

        <Form.List name="notes">
          {(fields, { add, remove }) => (
            <>
              {fields.map(({ key, name, ...restField }) => (
                <Space
                  key={key}
                  style={{ display: 'flex', marginBottom: 8 }}
                  align="baseline"
                >
                  <Form.Item
                    {...restField}
                    label="分享说明"
                    name={[name, 'rule']}
                    rules={[{ required: true, message: '请输入分享说明' }]}
                  >
                    <Input placeholder="分享说明" />
                  </Form.Item>
                  <MinusCircleOutlined onClick={() => remove(name)} />
                </Space>
              ))}
              <Form.Item>
                <Button
                  type="dashed"
                  onClick={() => add()}
                  block
                  icon={<PlusOutlined />}
                >
                  添加说明
                </Button>
              </Form.Item>
            </>
          )}
        </Form.List>

        <Form.List name="rules">
          {(fields, { add, remove }) => (
            <>
              {fields.map(({ key, name, ...restField }) => (
                <Space
                  key={key}
                  style={{ display: 'flex', marginBottom: 8 }}
                  align="baseline"
                >
                  <Form.Item
                    {...restField}
                    label="分享规则"
                    name={[name, 'rule']}
                    rules={[{ required: true, message: '请输入分享规则' }]}
                  >
                    <Input placeholder="分享规则" />
                  </Form.Item>
                  <MinusCircleOutlined onClick={() => remove(name)} />
                </Space>
              ))}
              <Form.Item>
                <Button
                  type="dashed"
                  onClick={() => add()}
                  block
                  icon={<PlusOutlined />}
                >
                  添加规则
                </Button>
              </Form.Item>
            </>
          )}
        </Form.List>
        <Form.Item wrapperCol={{ span: 4, offset: 10 }}>
          <Button type="link" htmlType="submit">
            保存
          </Button>
        </Form.Item>
      </Form>
    </Card>
  );
};

export default Share;
